import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import { FaBars, FaTimes} from "react-icons/fa"
import { MdFingerprint } from 'react-icons/md'
import { IconContext } from 'react-icons/lib'
import { Button } from '../Button'
import './Navbar.css'

function Navbar() {
    const [click, setClick] = useState(false)
    const [button, setButton] = useState(true)

    const handleClick =() => setClick(!click)
    const closeMobileMenu = () => setClick(false)
    window.addEventListener("resize", () => {
        if(window.innerWidth < 960){
            setButton(false)
        }
        else{
            setButton(true)
        }
    })
    return (
        <>
        <IconContext.Provider value={{color:"#fff"}}>

            <div className="navbar">
                <div className="navbar-container container">
                    <Link to="/" onClick={closeMobileMenu} className="navbar-logo">
                        <MdFingerprint className="navbar-icon"/>
                        Kushal's
                    </Link>
                    <div className="menu-icon" onClick={handleClick}>
                        {click ? <FaTimes /> : <FaBars/>}
                    </div>
                    <ul className={click ? "nav-menu active" : "nav-menu"}>
                        <li className="nav-item">
                            <Link to="/" onClick={closeMobileMenu} className="nav-links">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/services" onClick={closeMobileMenu} className="nav-links">Services</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/products" onClick={closeMobileMenu} className="nav-links">Products</Link>
                        </li>
                        <li className="nav-btn">
                            {button ? (
                                <Link to="/sign-up" className="btn-link">
                                    <Button buttonStyle= "btn--outline">Sign Up</Button>
                                </Link>
                            ): (
                                <Link to="/sign-up" className="btn-link">
                                    <Button buttonStyle="btn--primary" buttonSize="mobile">Sign Up</Button>
                                </Link>
                            )}
                        </li>
                    </ul>
                </div>
            </div>
        </IconContext.Provider>
        </>
    )
}

export default Navbar
